:root {
  --canopy-green: #014038;
  --meadow-mist: #96BAA0;
  --sky-bloom: #BDD4DA;
  --sunlit-petal: #F0C06D;
  --cloudy-grey: #F7F7F7;
  --canopy-green-t: #014038a7;
  --willow-gray: #C5C3C3;
}

/* Example:
h1 {
  font-size: rem(30);  <-- this is 1.875rem
}
*/
:root {
  /* Title Large */
  --font-titleLarge: "Arial", sans-serif;
  --size-titleLarge: 1.875rem;
  --weight-titleLarge: bold;
  /* Title */
  --font-title: "Arial", sans-serif;
  --size-title: 1.5625rem;
  --weight-title: bold;
  /* Subtitle */
  --font-subtitle: "Comfortaa", sans-serif;
  --size-subtitle: 0.9375rem;
  --weight-subtitle: normal;
  /* Body Large */
  --font-bodyLarge: "Calibri", sans-serif;
  --size-bodyLarge: 1.25rem;
  --weight-bodyLarge: normal;
  /* Body Strong */
  --font-bodyStrong: "Calibri", sans-serif;
  --size-bodyStrong: 1rem;
  --weight-bodyStrong: bold;
  /* Body */
  --font-body: "Calibri", sans-serif;
  --size-body: 1rem;
  --weight-body: normal;
  /* Captions */
  --font-captions: "Arial", sans-serif;
  --size-captions: 0.6875rem;
  --weight-captions: bold;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  text-decoration: none;
}

html {
  position: relative;
  min-height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background: var(--canopy-green);
  color: var(--willow-gray);
}

main {
  flex: 1;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}

@media (min-width: 69.375em) {
  h1 {
    font-size: 1.75rem;
  }
}
a,
a:visited,
a:active {
  text-decoration: none;
}

.titleLarge {
  font: var(--weight-titleLarge) var(--size-titleLarge) var(--font-titleLarge);
}

.title {
  font: var(--weight-title) var(--size-title) var(--font-title);
}

.subtitle {
  font: var(--weight-subtitle) var(--size-subtitle) var(--font-subtitle);
}

.bodyLarge {
  font: var(--weight-bodyLarge) var(--size-bodyLarge) var(--font-bodyLarge);
}

.bodyStrong {
  font: var(--weight-bodyStrong) var(--size-bodyStrong) var(--font-bodyStrong);
}

.body {
  font: var(--weight-body) var(--size-body) var(--font-body);
}

.captions {
  font: var(--weight-captions) var(--size-captions) var(--font-captions);
}

.container {
  padding: 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
}

@media (min-width: 54.375em) {
  .container {
    padding: 0;
  }
}
@media (max-width: 54.3125em) {
  .container {
    padding: 0rem;
    margin: 0;
  }
}
.homeContent {
  display: flex;
  text-align: center;
  justify-content: space-around;
}

@media (min-width: 54.375em) {
  .homeContent {
    display: flex;
    justify-content: space-around;
  }
}
.homeContent__categories, .homeContent__links {
  display: flex;
  flex-direction: column;
}

.homeContent__center {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 66%;
  margin: 0;
}

.homeContent__products, .homeContent__reviews {
  display: flex;
  flex-wrap: wrap;
  padding: auto;
  width: 100%;
  margin: 0 auto;
}

.homeContent__stuffBox {
  margin: 1rem auto;
  color: var(--canopy-green);
  background-color: var(--cloudy-grey);
  padding: 0.625rem;
  border: 0rem;
  border-radius: 1rem;
}

.homeContent__image {
  border-radius: 1.875rem;
}

.homeContent__featured-products {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.25rem;
}

.homeContent__product-card {
  border-radius: 1rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease;
  background-color: var(--meadow-mist);
}

.homeContent__product-card:hover {
  transform: translateY(-0.3125rem);
}

.homeContent__product-card__image {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.homeContent__product-card__image img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.homeContent__product-card__details {
  padding: 0.9375rem;
  background-color: var(--meadow-mist);
}

.homeContent__product-card__title {
  margin: 0.625rem 0 0.3125rem;
  color: var(--canopy-green);
}

.homeContent__product-card__price {
  color: var(--canopy-green);
}

.homeContent__reviews {
  margin: auto;
}

.homeContent__reviews__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.25rem;
  padding: 1.25rem auto;
}

.homeContent__reviews__grid .container {
  padding: 0;
}

@media (max-width: 54.3125em) {
  .homeContent__reviews__grid {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 54.3125em) {
  .homeContent {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
  }
  .homeContent__featured-products {
    padding: 0.5rem;
    grid-template-columns: repeat(1, 1fr);
  }
  .homeContent__center {
    padding: 0rem;
    width: 100%;
    max-width: 100%;
  }
  .homeContent__categories, .homeContent__products, .homeContent__reviews, .homeContent__links {
    display: flex;
    margin: 0 auto;
    width: 100%;
    justify-content: center;
  }
  .homeContent__stuffBox {
    margin: 1.5rem auto;
    width: 95%;
  }
  .homeContent__image {
    border-radius: 1rem;
  }
}
.category,
.link {
  display: block;
  background-color: var(--meadow-mist);
  padding: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 0.3125rem;
  border: 0.1875rem solid var(--sky-bloom);
  transition: all 0.5s;
  border-radius: 1rem;
  position: center;
}

.product,
.review {
  display: inline-block;
  margin-bottom: 1.875rem;
  background-color: var(--meadow-mist);
  border: 0.1875rem solid var(--sky-bloom);
  padding: 0.625rem;
  border-radius: 1rem;
  width: 18.75rem;
  height: 21.875rem;
}

.review {
  height: 4.6875rem;
}

.category:hover,
.product:hover,
.review:hover,
.link:hover {
  background-color: var(--sky-bloom);
  color: var(--canopy-green);
  border-color: var(--sunlit-petal);
  box-shadow: 0rem 0.3125rem 0.625rem rgba(0, 0, 0, 0.2);
  transform: scale(1.01);
  transition: all 0.5s ease-out;
}

.aboutus {
  justify-content: center;
  text-align: center;
}

.aboutus__image {
  border-radius: 3.125rem;
  max-width: 100%;
}

.aboutus__stuffBox {
  margin: 1rem auto;
  padding: 1rem;
  color: var(--canopy-green);
  background-color: var(--meadow-mist);
  border: 0rem;
  border-radius: 0.9375rem;
  width: 95%;
}

.aboutus__formBox {
  margin: 1.5rem auto;
}

.aboutus input[type=text],
.aboutus input[type=submit],
.aboutus button {
  margin: 0rem auto 1rem;
  border: 0 solid var(--sky-bloom);
  padding: 0 1rem;
  height: 1.875rem;
  width: 50%;
  min-width: 15.625rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--cloudy-grey);
}

.aboutus__message {
  padding: 1rem;
  border: 0 solid var(--sky-bloom);
  width: 50%;
  min-width: 15.625rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--cloudy-grey);
}

.googlemap {
  padding-bottom: 50%;
  position: relative;
}

.googlemap iframe {
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  position: absolute;
}

.form-message {
  margin: 1rem auto;
  width: 50%;
  padding: 0.5rem;
  border-radius: 1.25rem;
  color: var(--canopy-green);
  background-color: var(--sky-bloom);
  display: none;
}

.form-message.error {
  background-color: red;
}

.form-message.success {
  background-color: var(--sky-bloom);
}

.footer {
  background-color: var(--sky-bloom);
  color: var(--canopy-green);
  padding: 0.9375rem 6.25rem;
  display: flex;
  justify-content: space-around;
  width: 100%;
}

@media (min-width: 54.375em) {
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.footer p {
  margin: 0.625rem 0 0.625rem;
  text-align: start;
}

@media (max-width: 54.3125em) {
  .footer {
    flex-direction: column;
    padding: 0.625rem 0;
    align-items: center;
    text-align: center;
  }
  .footer p {
    text-align: center;
  }
}
.footer__logo {
  margin: 0 auto;
}

.footer__title {
  margin-top: 0.3125rem;
}

.footer__copywrite {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 1.25rem auto 0;
  text-align: center;
}

.footer__copywrite p {
  margin: 0;
}

.footer__first--coDetails, .footer__second--company, .footer__third--quickLinks, .footer__fourth--socials {
  display: flex;
  flex-direction: column;
}

.footer__socialsTop {
  margin-bottom: 0.625rem;
  gap: 0.9375rem;
}

.footer__socialsBottom {
  margin-top: 0.625rem;
  margin-bottom: 2.5rem;
}

.footer__socialLinks a:link {
  color: var(--canopy-green);
}

.footer__socialLinks a:visited {
  color: var(--canopy-green);
}

.footer__socialLinks a:hover {
  color: var(--meadow-mist);
}

.footer__socialLinks a:active {
  color: var(--sunlit-petal);
}

.footer #newsletterForm {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
}

.footer #newsletterForm input[type=email] {
  padding-left: 0.625rem;
  box-sizing: border-box;
  border: 0 solid var(--sky-bloom);
  height: 1.875rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--meadow-mist);
}

.footer #newsletterForm button[type=submit] {
  padding: 0.125rem;
  box-sizing: border-box;
  border: 0 solid var(--sky-bloom);
  height: 1.875rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--meadow-mist);
}

.popup {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: var(--canopy-green-t);
}

.popup-content {
  background-color: var(--sky-bloom);
  margin: 15% auto;
  padding: 1.25rem;
  width: 50%;
  border-radius: 1.25rem;
  color: var(--canopy-green);
}

.popup-content h2 {
  margin: auto;
  text-align: center;
}

.close {
  color: var(--canopy-green);
  float: right;
  font-size: 1.75rem;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: var(--sunlit-petal);
  text-decoration: none;
  cursor: pointer;
}

.header {
  background-color: var(--sky-bloom);
  color: var(--canopy-green);
  padding: 0.9375rem 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header a {
  color: var(--canopy-green);
}

.header__hamburger {
  display: none;
}

.header__logo {
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.header__search, .header__searchbar, .header__shoppingcart, .header__account {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: auto;
  margin-right: 0.625rem;
}

.header__search i:hover, .header__searchbar i:hover, .header__shoppingcart i:hover, .header__account i:hover {
  color: var(--meadow-mist);
}

.header__search {
  margin-right: 0.125rem;
}

.header input[type=text] {
  padding-left: 0.625rem;
  box-sizing: border-box;
  border: 0 solid var(--sky-bloom);
  height: 1.875rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--meadow-mist);
}

.header__upper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 3.125rem;
}

.header__left {
  display: flex;
  justify-content: flex-start;
  flex: 1;
  margin-right: calc(6.875rem -rem(46.55));
}

.header__center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0.625rem;
  margin: 0 auto;
  text-align: center;
  position: center;
}

.header__right {
  display: flex;
  justify-content: flex-end;
  flex: 1;
}

.header__lower {
  position: absolute;
  padding: 1.25rem;
  left: 0;
  right: 0; /* makes the nav full-width */
  bottom: 0; /* positions the nav at the bottom of the header */
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header__lower ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.header__lower li {
  padding: 0 1.875rem;
}

.header__lower a {
  color: var(--canopy-green);
  text-decoration: none;
}

.header__responsive {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--meadow-mist);
  padding: 1.25rem;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 999;
}

.header__responsive a {
  display: block;
  width: 100%;
  padding: 0.625rem;
  text-align: center;
  color: var(--canopy-green);
  text-decoration: none;
}

@media (min-width: 54.375em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header__responsive {
    display: none;
  }
  .header__hamburger {
    display: none;
  }
  .header__lower {
    display: flex;
  }
}
@media (max-width: 54.3125em) {
  .header {
    padding: 0.625rem 0;
  }
  .header__upper {
    margin-bottom: 0rem;
  }
  .header__lower {
    display: none;
  }
  .header__hamburger {
    display: flex;
    cursor: pointer;
  }
  .header input[type=text], .header__lower, .header__account {
    display: none;
  }
}
.shop {
  display: flex;
  justify-content: space-around;
}

.shop__categories {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-left: 1.25rem;
}

.shop__products {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.25rem;
  padding: 1.25rem;
  width: 80%;
}

.shop .product-card {
  position: relative; /* Add this to position the message */
  border-radius: 0.5rem;
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease;
  background-color: var(--meadow-mist);
}

.shop .product-card:hover {
  transform: translateY(-0.3125rem);
}

.shop .product-card__image img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.shop .product-card__details {
  display: flex;
  padding: 0.9375rem;
  background-color: var(--meadow-mist);
}

.shop .product-card__details-left {
  flex: 1;
}

.shop .product-card__details-right {
  display: flex;
  align-items: center;
  position: relative; /* Add this to position the message */
}

.shop .product-card .cart-btn:hover {
  color: var(--sunlit-petal);
}

.shop .product-card .cart-btn, .shop .product-card__title, .shop .product-card__price {
  color: var(--canopy-green);
}

.shop .product-card__title {
  margin: 0 0 0.3125rem;
}

.shop .product-card__price {
  margin: 0;
}

@media (max-width: 54.3125em) {
  .shop {
    flex-direction: column-reverse;
    justify-content: center;
  }
  .shop__products {
    width: 100%;
    padding: 0.5rem;
    grid-template-columns: repeat(1, 1fr);
  }
  .shop__categories {
    margin: 0;
  }
}
.cart-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--canopy-green);
  color: white;
  padding: 0.3125rem 0.625rem;
  border-radius: 0.3125rem;
  font-size: 0.9rem;
  z-index: 10;
  animation: stayAndFade 6s forwards;
}

@keyframes stayAndFade {
  0% {
    opacity: 1;
  }
  67% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: var(--canopy-green-t);
}

.modal-content {
  background-color: var(--sky-bloom);
  margin: 15% auto;
  padding: 1.25rem;
  width: 50%;
  border-radius: 1.25rem;
}

.modal-content__button {
  border: var(--meadow-mist) solid 0.125rem;
  background-color: var(--meadow-mist);
  border-radius: 0.625rem;
  height: 1.875rem;
  padding: 0 0.9375rem;
}

.modal-content__button:hover {
  background-color: var(--sunlit-petal);
  text-decoration: none;
  cursor: pointer;
}

.close {
  color: var(--canopy-green);
  float: right;
  font-size: 1.75rem;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: var(--sunlit-petal);
  text-decoration: none;
  cursor: pointer;
}

.reviews {
  margin: auto;
}

.reviews__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.25rem;
  padding: 1.25rem 0;
}

.reviews__item {
  border-radius: 0.625rem;
  margin: 0 0 auto;
}

.reviews__image img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 0.625rem;
}

.reviews__review {
  border: var(--sunlit-petal) 0.1875rem solid;
  background-color: var(--meadow-mist);
  color: var(--canopy-green);
  padding: 0.5rem;
}

.reviews__review .bodyLarge {
  margin: 0;
}

.reviews__review .star-rating {
  display: inline-block;
}

.reviews__review .star-rating i {
  color: var(--sunlit-petal);
  font-size: 1.5rem;
  margin-right: 0.3125rem;
}

@media (max-width: 54.3125em) {
  .reviews__grid {
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
  }
  .reviews__grid #reviews_image1 {
    order: 1;
  }
  .reviews__grid #reviews_review1 {
    order: 2;
  }
  .reviews__grid #reviews_image2 {
    order: 3;
  }
  .reviews__grid #reviews_review2 {
    order: 4;
  }
  .reviews__grid #reviews_image3 {
    order: 5;
  }
  .reviews__grid #reviews_review3 {
    order: 6;
  }
}